$mainColor : #f00;


.red{
    color: $mainColor;
}

@mixin 함수 {
    background: #eee;
    padding: 20px;
    margin: 0 auto;
    max-width: 500px;
    width: 100%;
}

.alert{
    @include 함수()
}
.alert_2{
    @extend .alert;
    background: #ff5910;
}
.alert > p{
    margin: 0;
}